.work__filters {
  display: flex;
  align-items: center;
  column-gap: 1.875rem;
  margin: 2.5rem;
}

.work__item {
  cursor: pointer;
  font-weight: var(--font-bold);
  transition: .3s;
}

.work__item:hover {
  color: var(--first-color);
  
}

.work__countainer {
  grid-template-columns: repeat(3,1fr);
  gap: 1.875rem;
}

.work__card {
  position: relative;
  overflow: hidden;
  border-radius: var(--border-radius);
  box-shadow: var(--shadow);
  height: 93%;
}

.work__mask {
  background: #6c6ce5;
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  transition: .3s;
  opacity: 0;
}

.work__card:hover .work__mask {
  opacity: 0.9;
}

.work__category {
  color: #fff;
  background-color: var(--first-color);
  border-bottom-left-radius: 0.98rem ;
  border-bottom-right-radius: 0.98rem ;
  position: absolute;
  top: 0;
  left: 1.5rem;
  font-size: var(--small-font-size);
  display: inline-block;
  padding: 0.19rem 0.625rem;
  transform: translateY(-40px);
  transition: .3s;
}

.work__title {
  color: #fff;
  font-size: var(--h3-font-size);
  margin: 0 0 0 0.98rem;
  padding: 0 1.25rem;
  position: absolute;
  top: 3.75rem;
  transition: .3s;
  opacity: 0;
}

.work__button {
  color: #fff;
  position: absolute;
  bottom: 1.5rem;
  left: 1.5rem;
  font-size: var(--h3-font-size);
  display: block;
  height: 40px;
  width: 40px;
  background-color: #ffd15c;
  cursor: pointer;
  border-radius: 50%;
  text-align: center;
  line-height: 42px;
  transition: .3s;
  opacity: 0;
}

.work__card:hover .work__title , .work__card:hover .work__category {
  opacity: 1;
  transform: translateY(0);
}

.work__card:hover .work__button  {
  opacity: 1;
}

.work__thumbnail img {
  width: 100%;
  height: 100%;
  background-size: cover;
}



@media (max-width:1024px) {

  .work__filters {
    justify-content: center;
  }
  .work__countainer {
    grid-template-columns: repeat(2,330px);
    justify-content: center;
    row-gap: 1.875rem;
  }
}

@media (max-width:768px) {
  .work__countainer {
    grid-template-columns: 319px;
  }

  .work__filters {
     row-gap: .25rem;
     flex-wrap: wrap;
  }
}

@media (max-width:350px) {
  .work__container {
    grid-template-columns: 1fr;
  }
}